*{
    font-family: 'Courier New', Courier, monospace;   
}

body{
    padding: 60px;
}

input{
    border-radius: 5px;
    font-size: 1.2rem;
    height: 30px;
    width: 100%;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card{
    align-content: center;
    border: 1px gray solid;
    border-radius: 5px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p{
    padding: 5px;
    text-align: center;
    max-height: 170px;

}